@media (hover: hover) {
  .label a:hover {
    --tw-text-opacity: 1;
    color: hsl(var(--bc) / var(--tw-text-opacity));
  }

  .menu li > :not(ul):not(details):active, .menu li > :not(ul):not(details).active, .menu li > details > summary:active {
